.navScrolled {
  position: fixed;
  display: flex;
  width: 100%;
  padding: 0.5rem 2rem 0.5rem 2rem;
  justify-content: space-between;
  border-bottom: 0.5px solid rgb(228, 228, 228);
  background-color: white;
  z-index: 10;
}

.navNotScrolled {
  position: fixed;
  display: flex;
  width: 100%;
  padding: 0.5rem 2rem 0.5rem 2rem;
  justify-content: space-between;
  background-color: transparent;
  z-index: 10;
}

.scrolledNavItem {
  font-size: 0.9rem;
  font-weight: bold;
  border-radius: 8px;
  color: rgb(146, 146, 146);
  padding: 0.25rem 1rem 0.25rem 1rem;
  cursor: pointer;
}

.scrolledNavItem:hover {
  font-size: 0.9rem;
  font-weight: bold;
  border-radius: 8px;
  background-color: #ff0066;
  color: white;
  padding: 0.25rem 1rem 0.25rem 1rem;
}

.unScrolledNavItem {
  font-size: 0.75rem;
  font-weight: bold;
  border-radius: 6px;
  color: white;
  padding: 0.2rem 0.8rem 0.2rem 0.8rem;
  cursor: pointer;
}

.unScrolledNavItem:hover {
  font-size: 0.75rem;
  font-weight: bold;
  border-radius: 6px;
  background-color: white;
  color: #ff0066;
  padding: 0.2rem 0.8rem 0.2rem 0.8rem;
}

.seeAllButton {
  display: flex;
  justify-content: center;
  background-color: #ede9fe;
  border-radius: 0.125rem;
  padding: 0.5rem 0 0.5rem 0;
  cursor: pointer;
}

.seeAllButton:hover {
  background-color: #ddd6fe;
}

.seeAll {
  display: flex;
  align-items: baseline;
  font-size: 0.75rem;
  line-height: 1.25rem;
  font-weight: bold;
  color: #8b5cf6;
  text-transform: uppercase;
}

.seeAllButton:hover > .seeAll > .arrow {
  font-size: 20px;
  transform: translateX(8px);
  transition: transform 0.3s ease-in-out;
}

.arrow {
  font-size: 20px;
  transform: translateX(0);
  transition: transform 0.3s ease-in-out;
}

.join::before {
  position: absolute;
  content: '';
  left: 2rem;
  margin-top: 6.5rem;
  background-color: grey;
  opacity: 0.25;
  height: 1px;
  width: 34vw;
}

.join::after {
  position: absolute;
  content: '';
  right: 2rem;
  margin-top: 6.5rem;
  background-color: grey;
  opacity: 0.25;
  height: 1px;
  width: 34vw;
}

.tabBackground {
  width: 100%;
  padding: 2rem;
  margin: 8rem 0 4rem 0;
  background-image: #ff0000;
  background-image: radial-gradient(
      at 62% 24%,
      hsla(28, 100%, 74%, 1) 0,
      transparent 50%
    ),
    radial-gradient(at 98% 6%, hsla(189, 100%, 56%, 1) 0, transparent 50%),
    radial-gradient(at 0% 50%, hsla(355, 85%, 93%, 1) 0, transparent 50%),
    radial-gradient(at 49% 81%, hsla(340, 100%, 76%, 1) 0, transparent 50%),
    radial-gradient(at 0% 100%, hsla(22, 100%, 77%, 1) 0, transparent 50%),
    radial-gradient(at 100% 100%, hsla(242, 100%, 70%, 1) 0, transparent 50%),
    radial-gradient(at 28% 28%, hsla(343, 100%, 76%, 1) 0, transparent 50%);
}

.tabTitle {
  line-height: 1.5rem;
  font-weight: bold;
  color: white;
  opacity: 0.5;
  cursor: pointer;
}

.tabSelected {
  line-height: 1.5rem;
  font-weight: bolder;
  color: white;
  opacity: 1;
  cursor: pointer;
}

.tabTitle:hover {
  opacity: 1;
}

.backBlur {
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.25);
}

.flip-card {
  background-color: transparent;
  width: 300px;
  height: 200px;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateX(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: white;
  border: 0.5px solid #f1f1f1;
  border-radius: 4px;
  color: black;
}

.flip-card-back {
  background-color: white;
  color: black;
  border-radius: 4px;
  transform: rotateX(180deg);
}

